import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { ClientUser } from '@core/models';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { DestroyService } from '@shared/services/destroy.service';
import { Observable, takeUntil } from 'rxjs';

@Component({
    selector: 'findqo-my-profile-v2',
    templateUrl: './my-profile-v2.component.html',
    styleUrls: ['./my-profile-v2.component.scss'],
    providers: [DestroyService],
    standalone: false
})
export class MyProfileV2Component implements OnInit {
	constructor(
		public myProfileFacade: MyProfileFacade,
		private destroy: DestroyService,
		private readonly router: Router
	) {
		this.user$ = myProfileFacade.getUser$();
		this.showBusinessAcc$ = myProfileFacade.getShowBusinessAcc$();
		this.tabIndex$ = myProfileFacade.getProfileTabIndex$();
	}

	user$: Observable<ClientUser>;
	showBusinessAcc$: Observable<boolean>;
	tabIndex$: Observable<number>;

	isSubscribed = false; // TODO: Once the user subscribe only the subscription page will be shown
	hasBusinessAccount = false;

	handleTabChange(index): void {
		if (index !== 1) {
			this.router.navigate([APP_ROUTE.myProfile]);
		}
	}

	ngOnInit(): void {
		this.user$.pipe(takeUntil(this.destroy)).subscribe((user: ClientUser) => {
			this.hasBusinessAccount = user?.businessProfile?.name ? true : false;
		});

		this.showBusinessAcc$.pipe(takeUntil(this.destroy)).subscribe((showSubscribe: boolean) => {
			this.hasBusinessAccount = showSubscribe;
		});

		this.router.routerState.root.queryParams.pipe(takeUntil(this.destroy)).subscribe((params) => {
			this.myProfileFacade.setProfileTabIndex(params['tab'] ? params['tab'] : 0);
		});
	}
}
