import { Component, OnInit } from '@angular/core';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FeatureVersion } from '@core/enums';
import { environment as env } from '@environments/environment';
import { DestroyService } from '@shared/services/destroy.service';
import { Observable, takeUntil } from 'rxjs';

import { VALIDATOR } from '@core/constants';
import { ViewportService } from '@core/services/viewport.service';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';

@Component({
    templateUrl: './my-profile.page.html',
    styleUrls: ['./my-profile.page.scss'],
    providers: [{ provide: MAT_DIALOG_DATA, useValue: {} }, DestroyService],
    standalone: false
})
export class MyProfilePage implements OnInit {
	constructor(
		public viewport: ViewportService,
		private fb: UntypedFormBuilder,
		private myProfileFacade: MyProfileFacade,
		private destroyService: DestroyService
	) {
		this.isLoading$ = myProfileFacade.getIsLoading$();
		this.user$ = myProfileFacade.getUser$();
	}

	readonly featureVersion = FeatureVersion;

	loading = false;
	userDetails: any;
	checkEmailPreference: boolean;

	phone = '';
	newPhone = '';

	selectedFile: File | null = null;
	isPhotoUploading = false;
	defaultUserImg: string = env.defaultAvatarImg;
	isPhotoUploaded = false;

	form = this.fb.group({
		name: ['', [Validators.required, VALIDATOR.pattern.name]],
	});

	isLoading$: Observable<boolean>;
	user$: Observable<any>;

	onSubmit(): void {
		this.myProfileFacade.updateProfile(this.form.value);
	}

	showUserDetails() {
		this.user$.pipe(takeUntil(this.destroyService)).subscribe((response) => {
			if (response) {
				this.userDetails = response;
				this.form.get('name').setValue(response?.name || '');
				this.checkEmailPreference = response?.preference?.marketingEmailAlerts === '1';
			}
		});
	}

	ngOnInit(): void {
		this.showUserDetails();
		this.myProfileFacade.loadUser();
	}
}
